<template>
  <div>
    <v-row class="mb-5 mt-2">
      <v-spacer></v-spacer>
      <v-dialog v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">Certificate Information</span>
          </v-card-title>
          <v-card-text>
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    label="Title*"
                    required
                    v-model="title"
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-container>
            <small>*indicates required field</small>
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="warning" dark @click="dialog = false">
              <v-icon left>
                mdi-close
              </v-icon>
              Close
            </v-btn>
            <v-btn color="primary" dark @click="certificateCreateHandler">
              <v-icon left>
                mdi-content-save
              </v-icon>
              Save
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";

export default {
  name: "CertificateForm",
  data: () => ({
    menu: false,
    dialog: false
  }),
  computed: {
    ...mapGetters("certificate", ["getCertificateCreated", "getCertificate"]),
    title: {
      get() {
        return this.$store.state.certificate.certificate.title;
      },
      set(value) {
        this.$store.commit("certificate/updateTitle", value);
      }
    }
  },
  methods: {
    ...mapActions("certificate", ["addCertificate", "fetchCertificate"]),
    async certificateCreateHandler() {
      await this.addCertificate();
      if (this.getCertificateCreated == true) {
        this.dialog = false;
      }
    },
    async showFormHandler(certificateId) {
      this.clearForm();
      if (certificateId != null) {
        await this.fetchCertificate(certificateId);
      }
      this.dialog = true;
    },
    clearForm() {
      this.title = null;
    }
  }
};
</script>

<style scoped></style>
